import defaultState from './state'
import * as constants from './constants'

export default (state = defaultState, action) => {
  switch (action.type){
    case constants.SET_DATE_TIME://
      return state.set('dataTime',action.dataTime)
    case constants.OPEN_MESSAGE_BOX: //
      return state.set('isOpenMessageBox',action.data)
    case constants.CLOSE_MESSAGE_BOX: //
      return state.set('isOpenMessageBox',action.data)
    case constants.OPEN_START_BOX: //
      return state.set('isOpenStartBox',action.data)
    case constants.CLOSE_START_BOX: //
      return state.set('isOpenStartBox',action.data)
    case constants.ADD_WINDOW_LIST: //
      return state.set('openWindowList',action.openWindowList)
    case constants.SHOW_WINDOW://
      return state.set('openWindowList',action.windowList)
    case constants.CLOSE_WINDOW://
      return state.set('openWindowList',action.openWindowList)
    case constants.HIDDEN_WINDOW://
      return state.set('openWindowList',action.windowList)
    case constants.LOAD_WINDOW://
      return state.set('openWindowList',action.windowList)
    case constants.SET_BACKGROUND://
      return state.set('background',action.backgroundDesk)
    case constants.SET_CONTEXT_MENU_LIST://
      return state.set('contextMenu',action.list)
    case constants.SET_DESKTOP_APPS_SHOW_CONTROL://
      return state.set('desktopAppsShowControl',action.data)
    default:
      return state;
  }
}