import { fromJS } from 'immutable'
import * as constants from './constants'

/*
 * 
 */
export const empty_list = (list) => {
  return {
    type: constants.EMPTY_LIST,
    list: fromJS(list)
  }
}
/*
 * 
 */
export const set_file_list = (list) => {
  return {
    type: constants.SET_FILE_LIST,
    list: fromJS(list)
  }
}