import React, {Component, Fragment} from 'react'
import { Button,Tree } from '@alifd/next'

const TreeNode = Tree.Node

const dataList =
  {id:0,name:'',url:null,children:[
    {id:1,name:'',url:'/win/system',children:[
        {id:2,name:'',url:'/win/system',children:[
            {id:3,name:'',url:'/win/system',children:null},
            {id:4,name:'',url:'/win/system',children:null}
        ]},
        {id:5,name:'',url:'/win/system',children:[
            {id:6,name:'',url:'/win/system',children:null},
            {id:7,name:'',url:'/win/system',children:null},
            {id:8,name:'',url:'/win/system',children:null},
            {id:9,name:'',url:'/win/system',children:null}
        ]}
    ]},
    {id:10,name:'',url:'',children:null},
    {id:11,name:'',url:'',children:null}
  ]}

class PrivilegeTree extends Component {

  state = {}

  render() {
    const { isShow } = this.props
    return (
      <Tree checkable={!isShow} defaultExpandAll showLine>
        <TreeNode label={dataList.name}>
          {this.tree(dataList.children)}
        </TreeNode>
      </Tree>
    )
  }
  tree = (data) => {
    const { isShow } = this.props
    return data.map((row)=>{
      if(row.children){
        return (
          <TreeNode
            key = {row.id}
            label={
              <div className="privilege_manage_tree_leaf">
                <span>{row.name}</span>
                {!isShow && <Button size="small"></Button>}
                </div>
            }
          >
            {row.children && this.tree(row.children)}
          </TreeNode>
        )
      }else{
        return (
            <TreeNode
              key = {row.id}
              label={
                <div className="privilege_manage_tree_leaf">
                  <span>{row.name}</span>
                  {!isShow &&
                    <Fragment>
                      <Button size="small"></Button>
                      <Button size="small"></Button>
                    </Fragment>}
                </div>
              }
            />
        )
      }
    })
  }
}


export default PrivilegeTree