import React, {Component} from 'react'
import { Tab,Button,Checkbox,Icon } from '@alifd/next'
import { View } from 'react-desktop'

import "../../../../../public/style/windows/system_control/desktop_app_manage.scss"

import ContentRight from "../../../../../components/content/ContentRight"
import AppSet from "../../../../../components/block/AppSetButton"

const appsList = {
  system:{
    title:'',
    list:[
      {name:'',img:'https://react-desktop.oss-cn-shenzhen.aliyuncs.com/images/logo/finder.png',type:'app1',sort:1},
      {name:'1',img:'https://react-desktop.oss-cn-shenzhen.aliyuncs.com/images/logo/finder.png',type:'app2',sort:2}
    ]
  },
  customize:{
    title:'',
    list:[
      {name:'1',img:'https://react-desktop.oss-cn-shenzhen.aliyuncs.com/images/logo/finder.png',type:'app1',sort:1},
      {name:'2',img:'https://react-desktop.oss-cn-shenzhen.aliyuncs.com/images/logo/finder.png',type:'app2',sort:2}
    ]
  }
}
//
const tabs = [
  {title:'',type:'system'},
  {title:'',type:'customize'}
]

const breadcrumbRoute = {
  title:'',
  url:"/win/system",
  list:[
    {name:"",url:'/desktop_app_manage'}
  ]
}

class Index extends Component {

  state = {
    system:[],
    customize:[]
  }

  render() {
    console.log(this)
    return (
      <ContentRight
        className="desktop_app_manage"
        breadcrumbRoute={breadcrumbRoute}
      >
        <Button className="desktop_app_manage_save"  type="primary"></Button>
        <Tab>
          {tabs.map(tab=>(
            <Tab.Item title={tab.title} key={tab.type}>
              <View style={{flexWrap:'wrap'}}>
                {appsList[tab.type].list.map(row=>(
                  <Checkbox
                    key={row.type}
                    checked={this.state[tab.type].indexOf(row.type) > -1 ? true : false }
                    onChange={(isCheck)=>{this.checkApp(tab.type,row.type,isCheck)}}
                  >
                    <View className="apps_list">
                      <div className="apps_list_img">
                        <img src={row.img} alt={row.img}/>
                      </div>
                      <div className="apps_list_title">
                        {row.name}
                      </div>
                      <View style={{alignItems:'center'}}>
                        <div className="apps_list_set">
                          <AppSet/>
                        </div>
                      </View>
                    </View>
                  </Checkbox>
                ))}
                <label className="next-checkbox-wrapper ">
                  <div className="apps_list apps_list_add">
                    <Icon type="add" />
                    <div className="apps_list_add_title">{tab.title}</div>
                  </div>
                </label>
              </View>
            </Tab.Item>
          ))}
        </Tab>
      </ContentRight>
    )
  }
  /*
   * @param String tab_type 
   * @param String app_type 
   * @param String isCheck 
   */
  checkApp = (tab_type,app_type,isCheck) => {
    const arr = this.state[tab_type]
    if(isCheck){
      arr.push(app_type)
    }else{
      const index = arr.indexOf(app_type)
      arr.splice(index,1)
    }
    this.setState({
      [tab_type]:arr
    })
  }
}


export default Index